export default "ശബരിമലയില്‍ പ്രവേശിച്ച ബിന്ദു അമ്മിണി വിശ്വാസിയല്ലെന്നും ആക്ടിവിസ്റ്റ് ആണെന്നും ഹൈക്കോടതി. ആക്ടിവിസ്റ്റുകളായ സ്ത്രീകളെ ശബരിമലയില്‍ പ്രവേശിപ്പിക്കുന്നതിനെതിരെ <b>ബി ജെ പി, ആര്‍.എസ്.എസ്. പ്രവര്‍ത്തകര്‍ പ്രക്ഷോഭം നടത്തിയപ്പോള്‍ സര്‍ക്കാര്‍ ആക്ടിവിസ്റ്റുകള്‍ കയറുന്നതിനെ പിന്തുണച്ചുവെന്നും ഹൈക്കോടതി</b> ചൂണ്ടിക്കാട്ടി<br><br>- മാതൃഭൂമി (Mar 5, 2021)<br><br><a href=\https://www.mathrubhumi.com/mobile/news/kerala/kerala-government-support-women-entry-to-sabarimala-says-kerala-high-court-1.5491258\>https://www.mathrubhumi.com/mobile/news/kerala/kerala-government-support-women-entry-to-sabarimala-says-kerala-high-court-1.5491258</a>"
